import { CustomControlledInput } from 'Components';
import { useEffect, useRef, useState } from 'react';
import { useFormContext, useWatch } from 'react-hook-form';

interface FiledsToWatch {
    HouseNumber: string;
    Zip: string;
    AptNumber: string;
    isDivided: string;
}

interface IHouseNumPicker {
    fields: FiledsToWatch;
    disabled?: boolean;
}
export const HouseNumPicker = ({ fields, disabled }: IHouseNumPicker) => {
    const [isDivided, setIsDivided] = useState(false);
    const { getValues } = useFormContext();

    const isCityDivided = useWatch({ name: fields.isDivided });
    const pickedHouseNum = useRef('');
    useEffect(() => {
        pickedHouseNum.current = getValues(fields.HouseNumber);
        setIsDivided(isCityDivided ? true : false);
    }, [isCityDivided]);

    return (
        <CustomControlledInput
            important={isDivided}
            Input={{
                type: 'number',
            }}
            name={fields.HouseNumber}
            disabled={disabled}
            label={"Dictionary.houseNum.he"}
        />
    );
};
