import { Box, InputAdornment, CircularProgress } from '@mui/material';
import {  CustomControlledInput } from 'Components';
import { useCitiesList, useDebounce } from 'Hooks';
import { useCallback, useEffect, useRef, useState } from 'react';
import { useFormContext, useWatch } from 'react-hook-form';

interface IFieldsForZip {
    HouseNumber: string;
    Entrance: string;
    CityId: string;
    StreetId: string;
    Zip: string;
}
interface IZipInput {
    disableSearch?: boolean;
    fields: IFieldsForZip;
}
export const ZipInput = ({ disableSearch, fields }: IZipInput) => {
    const [zipLoading, setZipLoading] = useState(false);
    const { setValue } = useFormContext();
    const cities = useCitiesList();
    const isFirstTime = useRef(true);

    const houseNum = useWatch({ name: fields.HouseNumber });
    const aptEntry = useWatch({ name: fields.Entrance });
    const cityId = useWatch({ name: fields.CityId });
    const streetId = useWatch({ name: fields.StreetId });
    const zip = useWatch({ name: fields.Zip });

    useEffect(() => {
        if (zip) isFirstTime.current = true;
    }, [zip]);

    const nullOrEmptry = (val?: any) => !val || val === '';

    const zipForAddress = useRef('');
    const onLoad = () => {
        if (isFirstTime.current || disableSearch) {
            isFirstTime.current = false;
        } else if (zipForAddress.current !== houseNum + aptEntry + cityId + streetId) {
            zipForAddress.current = houseNum + aptEntry + cityId + streetId;
            handleZip(houseNum + aptEntry + cityId + streetId);
        }
    };
    useEffect(onLoad, [houseNum, aptEntry, cityId, streetId]);

    const handleZip = useDebounce(async (id?: string) => {
        if (id && zipForAddress.current !== id) return;
        if (nullOrEmptry(cityId)) {
            // try to get the right city id if missing??
            // return setValue(fields.Zip, '');
            return;
        }
        if (cities && cities.find((x) => x.id === cityId)?.divided) {
            if (nullOrEmptry(streetId) || nullOrEmptry(houseNum)) return setValue(fields.Zip, '');
        }

        setZipLoading(true);
        // const entery = ENTRIES.find((x) => x.value == aptEntry);
        // const getZipResult = await getZip({
        //     CityID: cityId,
        //     StreetID: streetId,
        //     House: houseNum ? houseNum + '' : '',
        //     Entry: entery ? entery.text : '',
        // });
        // const res = getZipResult?.zip;
        // setValue(fields.Zip, res ? res : '');
        setZipLoading(false);
    }, 1000);

    return (
        <CustomControlledInput
            disabled
            Input={{
                type: 'number',
                endAdornment: (
                    <InputAdornment position='end' sx={{ ...(disableSearch && { display: 'none' }) }}>
                        {zipLoading ? (
                            <CircularProgress size={20} />
                        ) : (
<></>
                        )}
                    </InputAdornment>
                ),
            }}
            name={fields.Zip}
            {...(disableSearch ? { disabled: disableSearch } : null)}
            label={"Dictionary.zip.he"}
        />
    );
};
