import { Grid } from '@mui/material';
import * as yup from 'yup';
import { CustomControlledSelect, CustomControlledInput } from 'Components';
import { useFormContext } from 'react-hook-form';
import { ErrorMessages, patterns } from 'Components/Validation';
import { ZipInput } from './ZipInput';
import { StreetPicker } from './StreetPicker';
import { CityPicker } from './CityPicker';
import { HouseNumPicker } from './HouseNumPicker';

interface AddressBlock {
    baseName?: string;
    disabled?: boolean;
    fullWidth?: boolean;
}

export const AddressFieldsName = {
    City: 'City',
    CityId: 'CityID',
    Street: 'Street',
    StreetId: 'StreetID',
    HouseNumber: 'HouseNum',
    Entrance: 'HouseEntry',
    FloorNumber: 'FloorNumber',
    AptNumber: 'AptNum',
    Zip: 'Zip',
    Comments: 'Comments',
};

export const AddressBlock = (props: AddressBlock) => {
    const baseName = props.baseName ? props.baseName + '.' : '';
    const { setValue, getValues } = useFormContext();

    const fields = {
        City: baseName + AddressFieldsName.City,
        CityId: baseName + AddressFieldsName.CityId,
        Street: baseName + AddressFieldsName.Street,
        StreetId: baseName + AddressFieldsName.StreetId,
        HouseNumber: baseName + AddressFieldsName.HouseNumber,
        Entrance: baseName + AddressFieldsName.Entrance,
        AptNumber: baseName + AddressFieldsName.AptNumber,
        FloorNumber: baseName + AddressFieldsName.FloorNumber,
        Zip: baseName + AddressFieldsName.Zip,
        Comments: baseName + AddressFieldsName.Comments,
        isDivided: baseName + 'isDivided',
    };

    const onUpdateStreet = () => {
        setValue(fields.HouseNumber, '');
        setValue(fields.Entrance, '');
        setValue(fields.AptNumber, '');
        setValue(fields.Zip, '');
    };

    const cityChangeHandler = async () => {
        setValue(fields.Street, '');
        setValue(fields.StreetId, '');
        onUpdateStreet();
    };

    return (
        <Grid container item lg={props.fullWidth ? 12 : 10} spacing={4}>
            <Grid item sm={12} md={6}>
                <CityPicker
                    fieldName={fields.City}
                    fieldId={fields.CityId}
                    onChange={cityChangeHandler}
                    isDividedFieldName={fields.isDivided}
                    {...(props.disabled ? { disabled: props.disabled } : null)}
                />
            </Grid>
            <Grid item sm={12} md={6}>
                <StreetPicker
                    fieldName={fields.Street}
                    fieldId={fields.StreetId}
                    onUpdateStreet={onUpdateStreet}
                    cityIdFieldName={fields.CityId}
                    isDividedField={fields.isDivided}
                    {...(props.disabled ? { disabled: props.disabled } : null)}
                />
            </Grid>
            <Grid container item sm={12} md={6} spacing={1}>
                <Grid item sm={3} md={4}>
                    <HouseNumPicker fields={fields} {...(props.disabled ? { disabled: props.disabled } : null)} />
                </Grid>
                <Grid item sm={6} md={4}>
                    <CustomControlledSelect
                        emptyOption
                        {...(props.disabled ? { disabled: props.disabled } : null)}
                        name={fields.Entrance}
                        menuItems={[]}
                        label={"Dictionary.houseEntry.he"}
                    />
                </Grid>
                <Grid item sm={3} md={4}>
                    <CustomControlledInput
                        Input={{ type: 'number' }}
                        name={fields.AptNumber}
                        {...(props.disabled ? { disabled: props.disabled } : null)}
                        label={"Dictionary.apartmentNum.he"}
                    />
                </Grid>
            </Grid>
            <Grid container item sm={12} md={6} spacing={1}>
                <Grid item sm={6} md={6}>
                    <CustomControlledInput
                        Input={{ type: 'number' }}
                        name={fields.FloorNumber}
                        {...(props.disabled ? { disabled: props.disabled } : null)}
                        label={"Dictionary.floorNum.he"}
                    />
                </Grid>
                <Grid item sm={6} md={6}>
                    <ZipInput fields={fields} disableSearch={props.disabled} />
                </Grid>
            </Grid>
            <Grid item sm={12} md={12}>
                <CustomControlledInput
                    name={fields.Comments}
                    {...(props.disabled ? { disabled: props.disabled } : null)}
                    label={"Dictionary.comments.he"}
                />
            </Grid>
        </Grid>
    );
};

export interface IAddressBlock {
    City: string;
    CityID: string;
    StreetID: string;
    Street: string;
    Zip: string;
    HouseNum: number | undefined;
    FloorNumber: number | undefined;
    AptNum: number | undefined;
    HouseEntry: string;
    Comments?: string;
}

export const AddressBlockYupSchema = yup.object({
    [AddressFieldsName.City]: yup.string().required(ErrorMessages.required),
    [AddressFieldsName.CityId]: yup.string(),
    [AddressFieldsName.Street]: yup.string().test({
        message: ErrorMessages.required,
        test: async (val, context) => {
            const { isDivided } = context.parent;
            if (isDivided) return val ? true : false && val != '';
            return true;
        },
    }),
    [AddressFieldsName.StreetId]: yup.string(),
    [AddressFieldsName.HouseNumber]: yup
        .number()
        .transform((val) => (val ? val : null))
        .nullable()
        .max(9999, ErrorMessages.wrongInput)
        .test({
            message: ErrorMessages.required,
            test: async (val, context) => {
                const { isDivided } = context.parent;
                if (isDivided) return val ? true : false;
                return true;
            },
        }),
    [AddressFieldsName.AptNumber]: yup
        .number()
        .transform((val) => (val ? val : null))
        .nullable()
        .max(9999, ErrorMessages.wrongInput)
        .optional(),
    [AddressFieldsName.FloorNumber]: yup
        .number()
        .transform((val) => (val ? val : null))
        .nullable()
        .max(9999, ErrorMessages.wrongInput)
        .optional(),
    [AddressFieldsName.Entrance]: yup.string().optional(),
    [AddressFieldsName.Zip]: yup.string().optional().matches(patterns.number, ErrorMessages.numbersOnly),
    [AddressFieldsName.Comments]: yup
        .string()
        .optional()
        .matches(patterns.freeText, ErrorMessages.wrongInput)
        .max(75, ErrorMessages.tooLong),
});

export const AddressBlockInit: IAddressBlock = {
    AptNum: undefined,
    City: '',
    CityID: '',
    HouseEntry: '',
    HouseNum: undefined,
    FloorNumber: undefined,
    Street: '',
    StreetID: '',
    Zip: '',
    Comments: '',
};
