// import { Dictionary } from 'Dictionary';
import { useFormContext, useWatch } from 'react-hook-form';
import { useEffect } from 'react';
import { CustomControlledAutocomplete } from 'Components/CustomInputs';
import { useCitiesList } from 'Hooks';
import { Box } from '@mui/material'

interface ICityPicker {
    fieldName: string;
    fieldId: string;
    isDividedFieldName: string;
    onChange: () => void;
    disabled?: boolean;
}

export const CityPicker = ({ fieldId, fieldName, disabled, onChange, isDividedFieldName }: ICityPicker) => {
    const cities = useCitiesList();
    const { setValue } = useFormContext();

    const changeHandler = (val: string, id: string) => {
        setValue(isDividedFieldName, cities.find((x) => x.id == id)?.divided);
        onChange();
    };

    const cityId = useWatch({ name: fieldId });
    useEffect(() => {
        setValue(isDividedFieldName, cities.find((x) => x.id == cityId)?.divided);
    }, [cities, cityId]);

    return (
        <Box border='2px dotted red'>
            <CustomControlledAutocomplete
                important
                name={fieldName}
                nameId={fieldId}
                onBlur={changeHandler}
                options={cities}
                label={'Dictionary.settlement.he'}
                disabled={disabled}
            />
        </Box>
    );
};
